/**
 * 
 */
import LogUtils from './logUtils';
/**
 * 
 *  log 
 */
export function log(target, name, descriptor) {
    const fn = descriptor.value;
    descriptor.value = (...args) => {
        let result;
        LogUtils.groupCollapsed(`[d-utils] DecoratorUtils ${name}`, LogUtils.defaultColor);
        LogUtils.logDefault(`${name}(${args})`, `:`);
        LogUtils.logInfo(args, ': ');
        try {
            result = fn.apply(target, args);
            LogUtils.logSuccess(result, `:`);
        }
        catch (err) {
            LogUtils.logError(err, `:`);
        }
        LogUtils.groupEnd();
    };
}
/**
 * 
 * 
 */
export function fnTime(target, name, descriptor) {
    const fn = descriptor.value;
    if (typeof fn !== 'function') {
        LogUtils.logError(`${name}`, `[d-utils] fnTime : `);
        return;
    }
    return Object.assign(Object.assign({}, descriptor), { value() {
            console.time(`[d-utils] ${name}: `);
            try {
                return fn.apply(target, arguments);
            }
            finally {
                console.timeEnd(`[d-utils] ${name}: `);
            }
        } });
}
