import LogUtils from './logUtils';
/**
 * @description 
 * @param { funciton } callback while  boolean  true  
 * @param { number } loopTime   100
 * @param { number } timeout    10000  10
 * @return Promise
 */
export function wait(callback, loopTime = 100, timeout = 10000) {
    return new Promise((resolve, reject) => {
        if (typeof callback === 'function' && typeof callback() === 'boolean') {
            const t = setInterval(() => {
                if (callback()) {
                    clearTimeout(t);
                    clearTimeout(out);
                    resolve();
                }
            }, loopTime);
            const out = setTimeout(() => {
                LogUtils.logError('', '[d-utils] PromiseUtils wait timeout');
                clearTimeout(t);
                clearTimeout(out);
                reject();
            }, timeout);
        }
    });
}
/**
 * @description 
 * @param { promise[] } requestQueues   promise
 */
export function requestOnLoad(requestQueues) {
    return Promise.all(requestQueues);
}
/**
 * @description ,  timer
 * @param { number } timer    
 * @return promise
 */
export function sleep(timer) {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve();
        }, timer);
    });
}
/**
 * @description async / await    err!res 
 * @param { Promise } promise
 * @return { Array }  array [0] err array[1] data
 */
export function wrap(promise) {
    return promise
        .then((res) => [null, res])
        .catch((err) => [err, null]);
}
