/**
 * dom 
 */
import LogUtils from './logUtils';
/**
 * @description class
 * @param { HTMLElement } el dom
 * @param { String } className class
 * @example
 * hasClass(document.body, 'd-utils')
 */
export function hasClass(el, className) {
    return el.classList.contains(className);
}
/**
 * @description class
 * @param { HTMLElement } el dom
 * @param { String } className class
 * @example
 * hasClass(document.body, 'd-utils')
 */
export function addClass(el, className) {
    if (Array.isArray(className)) {
        className.forEach((item) => {
            if (!hasClass(el, item)) {
                el.classList.add(item);
            }
        });
        return;
    }
    if (!hasClass(el, className)) {
        el.classList.add(className);
    }
}
/**
 * @description class
 * @param { HTMLElement } el dom
 * @param { (String | Array) } className class
 * @example
 * removeClass(document.body, 'd-utils')
 */
export function removeClass(el, className) {
    if (Array.isArray(className)) {
        className.forEach((item) => {
            if (hasClass(el, item)) {
                el.classList.remove(item);
            }
        });
        return;
    }
    if (hasClass(el, className)) {
        el.classList.remove(className);
    }
}
/**
 * @description css
 * @param { HTMLElement } el dom
 * @param { String } cssProp css
 * @return { String } css
 * @example
 * computedStyle(document.body, 'width')
 */
export function computedStyle(el, cssProp) {
    if (!el) {
        LogUtils.logError('dom', '[d-utils] DomUtils computedStyle error => ');
        return;
    }
    if (!cssProp) {
        LogUtils.logError('css', '[d-utils] DomUtils computedStyle error => ');
        return;
    }
    return document.defaultView.getComputedStyle ? document.defaultView.getComputedStyle(el, '')[cssProp] : el.currentStyle[cssProp];
}
/**
 * @description jsfilter
 * @param { HTMLElement } el dom
 * @param { (String | Object) } type filter   bluropacitygrayscalesepiasaturatehue-rotateinvertbrightnesscontrastdrop-shadow, typeObjectfilter `bluropacitygrayscalesepiasaturatehue-rotateinvertbrightnesscontrastdrop-shadow
 * @param { (String | Number) } option  10px  10% type
 * @example
 * // filter
 * cssFilter(document.body, 'grayscale', 1)
 * // filter
 * cssFilter(document.body, {
 *   grayscale: 0.5,
 *   opacity: 0.7,
 *   'hue-rotate': '90deg'
 * })
 */
export function cssFilter(el, type, option) {
    if (typeof type === 'object' && !option) {
        let cssText = '';
        for (let k in type) {
            if (type.hasOwnProperty(k)) {
                cssText += `${k}(${type[k]})`;
            }
        }
        el.style.filter = cssText;
        el.style.webkitFilter = cssText;
        return;
    }
    el.style.filter = `${type}(${option})`;
    el.style.webkitFilter = `${type}(${option})`;
}
