import LogUtils from './logUtils';
import { GenericType } from './type';
/**
 * @description 
 * @param { object } options  
 * @property { String } title   
 * @property { String } body   
 * @property { String } icon   logo
 * @property { Function } show 
 * @property { Function } click 
 * @returns { Promise } resolve(options) 
 * @returns { Promise } reject(options) 
 * @example
 * const data = {
 *  title: 'notification',
 *  body: 'this is a test',
 *  logo: 'http://www.daiwei.org/index/images/logo/dw1.png'
 * }
 * notification(data)
 */
export function notification(options) {
    const defaultV = {
        title: '',
        body: 'Hello World !!!',
        icon: 'http://www.daiwei.org/index/images/logo/dw1.png',
        show: () => { },
        click: () => { }
    };
    let newOpt = Object.assign({}, defaultV, options);
    if (window.Notification && Notification.permission !== 'denied') {
        Notification.requestPermission(function () {
            let n = new Notification(newOpt.title, {
                body: newOpt.body,
                icon: newOpt.icon,
            });
            n.onshow = function () {
                newOpt.show();
            };
            n.onclick = function () {
                newOpt.click();
            };
        });
        return Promise.resolve(newOpt);
    }
    else {
        return Promise.reject(newOpt);
    }
}
/**
 * @description rgba
 * @param { Number } opacity     01
 * @return { String } rgba
 * @example
 * const color = randomColor(1)
 * console(color)
 */
export function randomColor(opacity = 1) {
    const r = ~~(Math.random() * 256);
    const g = ~~(Math.random() * 256);
    const b = ~~(Math.random() * 256);
    return `rgba(${r},${g},${b},${opacity})`;
}
/**
 * @description outline
 * @author Addy Osmani
 * @example
 * showLayoutFramework()
 */
export function layoutFramework() {
    Array.from(document.querySelectorAll('*'), function (a) { a.style.outline = '1px solid #' + (~~(Math.random() * (1 << 24))).toString(16); });
}
/**
 * @description  isStricttrue 2
 * @param { String } str  
 * @param { Boolean } isStrict  true 2; false 
 * @return { Number } 
 * @example
 * const str = 'd-utils'
 * console(calcStringLength(str))
 * console(calcStringLength(str, true))
 */
export function calcStringLength(str, isStrict) {
    if (typeof str !== 'string') {
        LogUtils.logError(`str must be string but found ${typeof str}`, '[d-utils] GenericUtils calcStringLength error => ');
        return;
    }
    if (!isStrict)
        return str.length;
    return Array.from(str).reduce((total, current) => {
        return total += current.charCodeAt(0) > 255 ? 2 : 1;
    }, 0);
}
/**
 * @description 
 * @param { String } str 
 * @param { Number } type  0: 1:  2:  3  
 * @return { String } 
 * @example
 * const str = ' d -js- ut ils '
 * // 0:  0
 * strTrim(str)
 * strTrim(str, 0)
 * @example
 * // 1: 
 * strTrim(str, 1)
 * @example
 * // 2: 
 * strTrim(str, 2)
 * @example
 * // 3: 
 * strTrim(str, 3)
 */
export function strTrim(str, type = GenericType.StrTrimType.LEFT_RIGHT) {
    if (typeof str !== 'string') {
        LogUtils.logError(`str must be string but found ${typeof str}`, '[d-utils] GenericUtils strTrim error => ');
        return;
    }
    switch (type) {
        case 0:
            return str.replace(/(^\s*)|(\s*$)/g, '');
        case 1:
            return str.replace(/\s/g, '');
        case 2:
            return str.replace(/(^\s*)/g, '');
        case 3:
            return str.replace(/(\s*$)/g, '');
        default:
            return str.replace(/(^\s*)|(\s*$)/g, '');
    }
}
/**
 * @description 
 * @param { Function } fn 
 * @param { Number } t  ms
 * @example
 * // resize1resize10console.log('resize')10
 * window.onresize = throttle(function () {
 * // es5 
 * let arg = Array.prototype.slice.call(arguments)
 * // es6 
 * let arg1 = Array.from(arguments)
 * console.log('resize-throttle', arg)
 * console.log('resize-throttle', arg1)
 * }, 1000)
 */
export function throttle(fn, t = 1000) {
    if (typeof fn !== 'function') {
        LogUtils.logError(``, '[d-utils] GenericUtils throttle error => ');
        return;
    }
    const _fn = fn;
    let time = null;
    let first = true;
    return function () {
        let arg = arguments;
        let _this = this;
        if (first) {
            _fn.apply(_this, arg);
            first = false;
            return;
        }
        if (time)
            return;
        time = setTimeout(function () {
            setTimeout(time);
            time = null;
            _fn.apply(_this, arg);
        }, t);
    };
}
/**
 * @description 
 * @param { Function } fn 
 * @param { Number } t  ms
 * @param { Boolean } immediate true: , false: ttt
 * @example
 * // resize11resize
 * window.onresize = debounce(function () {
 * // es5 
 * let arg = Array.prototype.slice.call(arguments)
 * // es6 
 * let arg1 = Array.from(arguments)
 * console.log('resize-debounce', arg)
 * console.log('resize-debounce', arg1)
 * }, 1000)
 */
export function debounce(fn, t, immediate = true) {
    if (typeof fn !== 'function') {
        LogUtils.logError(``, '[d-utils] GenericUtils debounce error => ');
        return;
    }
    let time;
    //  
    if (immediate) {
        return function () {
            clearTimeout(time);
            if (!time) {
                fn.apply(this, arguments);
            }
            time = setTimeout(function () {
                setTimeout(time);
                time = null;
            }, t);
        };
    }
    else {
        //  time 
        return function () {
            clearTimeout(time);
            time = setTimeout(function () {
                setTimeout(time);
                fn.apply(this, arguments);
                time = null;
            }, t);
        };
    }
}
/**
 * @description  
 * @param { Date } date     new Date()
 * @param { String } fmt  'yyyy-MM-dd hh:mm:ss'
 * @return { String } '2018-08-15 01:46:22'
 * @example
 * formatDate(new Date(), `yyyy-MM-dd hh:mm:ss`)
 * @example
 * formatDate(new Date(), `yyyy-MM-dd`)
 */
export function formatDate(date = new Date(), fmt = 'yyyy-MM-dd hh:mm:ss') {
    const newDate = new Date(date);
    let o = {
        'M+': newDate.getMonth() + 1,
        'd+': newDate.getDate(),
        'h+': newDate.getHours(),
        'm+': newDate.getMinutes(),
        's+': newDate.getSeconds(),
        'q+': ~~((newDate.getMonth() + 3) / 3),
        'S': newDate.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (newDate.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    for (let k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
        }
    }
    return fmt;
}
/**
 * @description 
 * @param { String } str 
 * @example
 * copyCode('hello world')
 */
export function copyCode(str) {
    const textArea = document.createElement('textarea');
    textArea.style.cssText = 'position: absolute; top: -1000px; right: -1000px; z-index: -1000;';
    document.body.appendChild(textArea);
    textArea.value = str;
    textArea.select();
    document.execCommand('copy');
    document.body.removeChild(textArea);
}
/**
 * @description base64
 * @param str 
 * @return str base64 
 */
export function base64Encode(str) {
    return window.btoa(str);
}
/**
 * @description base64
 * @param str base64
 * @return str
 */
export function base64Decode(str) {
    return window.atob(decodeURIComponent(str));
}
